
.listContainer{
  padding: 2rem;
  width: 90%;
  margin: 0 auto;

  h1{
    margin-bottom: 1rem;
  }
}
.products {
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  gap: 1rem;

  @include small {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }

  @include large {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
